<script>
import $ from 'jquery'
import 'select2' // globally assign select2 fn to $ element
import 'select2/dist/css/select2.min.css'

export default {
  props: {
    value: {
      type: String,
      default: '',
      required: true
    },
    errorMessages: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  watch: {
  },
  beforeDestroy () {
    $(this.$el).select2('destroy')
  },
  async mounted () {
    await this.$nextTick()
    var that = this
    var options = {
      theme: 'material',
      dropdownParent: $('#job-card'),
      multiple: false,
      createTag: true,
      width: '100%',
      placeholder: 'Search for a job',
      allowClear: false,
      ajax: {
        url: '/core4/api/v1/job/list',
        data: function (params) {
          var query = {
            search: params.term,
            page: params.page || 0
          }
          return query
        },
        processResults: function (data, params) {
          params.page = data.page
          var ret = data.data.map(function (item) {
            return { id: item._id, text: item._id }
          })
          return {
            results: ret,
            pagination: {
              more: data.page < data.page_count - 1
            }
          }
        }
      }
    }
    $(this.$el).select2(options)
    $(this.$el).on('change', function () {
      var item = $(this).select2('data')[0]
      that.$emit('input', item.id)
    })
    $('.select2-selection__arrow')
      // .addClass('material-icons')
      .html('↓')
  }
}
</script>

